<template>
  <div class="FallEventAdd">
    <v-dialog v-model="dialog">
      <template v-slot:activator="{ on }">
        <v-icon color="red lighten-2" dark v-on="on" @click="addProcess"
          >mdi-plus-circle</v-icon
        >
      </template>

      <v-card>
        <v-card-title :class="titleStyle" primary-title>{{
          title
        }}</v-card-title>

        <v-card-text>
          <v-form ref="form" class="mt-3">
            <v-alert outlined>
              <div class="d-flex justify-space-between">
                <span>姓名: {{ this.$store.state.uData.name }}</span>  
                <span>床號: {{ this.$store.state.uData.bed_name }}</span>
                <span>性別: {{ this.$store.state.uData.sex }}</span>
                <span>年齡: {{ this.$store.state.uData.birthday ? thisYear - this.$store.state.uData.birthday.split('-')[0] : '無資料' }}</span>
              </div>
                <span>主要診斷: {{ this.$store.state.uData.hadadisease }}</span>
            </v-alert>

            <div>
              <h2 class="grey lighten-3 pa-1">記錄內容</h2>
              <div class="d-flex">
                <v-text-field type="date" label="日期" v-model="list.Date" :rules="emptyRules"></v-text-field>
                <v-autocomplete :items="reasonItems" label="約束理由" v-model="list.reason" :rules="emptyRules" class="mx-1"></v-autocomplete>
                <v-autocomplete :items="equipmentItems" label="身體約束設備" v-model="list.equipment" :rules="emptyRules" class="mx-1" multiple></v-autocomplete>
                <v-autocomplete :items="partItems" label="身體約束部位" v-model="list.part" :rules="emptyRules" multiple></v-autocomplete>
              </div>
              <div class="d-flex">
                <v-autocomplete :items="zoneItems" label="身體約束時間" v-model="list.zone" :rules="emptyRules" multiple></v-autocomplete>
                <v-autocomplete :items="hourItems" label="每日身體約束持續時間(小時)" v-model="list.hour" :rules="emptyRules" class="mx-1"></v-autocomplete>
                <v-autocomplete :items="freereasonItems" label="解除身體約束原因" v-model="list.freereason" :rules="emptyRules" class="mx-1"></v-autocomplete>
                <v-checkbox label="已解除約束" v-model="list.releaseOK"></v-checkbox>
              </div>
            </div>
            
          </v-form>
        </v-card-text>
        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            class="primary"
            @click="addOK"
            v-if="processType == 'add'"
            :loading="loading"
            :disabled="loading"
            >確認新增</v-btn
          >
          <v-btn
            class="success"
            @click="editOK"
            v-if="processType == 'edit'"
            :loading="loading"
            :disabled="loading"
            >確認修改</v-btn
          >
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import dayjs from "dayjs";

export default {
  props:['useDataBase'],  //本表單使用的主要資料庫;用於讀取,新增,修改,刪除时指定的資料庫名
  data() {
    return {
      dialog: false,
      list: {}, //表單內資料
      processType: "", //存放頁面執行是add新增或edit修改
      title: "",
      titleStyle: "",
      emptyRules: [(v) => !!v || "不可空白"],
      loading: false,
      thisYear: dayjs().format("YYYY"),

      reasonItems:['預防跌倒','預防自拔管路','預防自傷','行為紊亂','協助治療','其他'],
      equipmentItems:['約束帶','約束背心','手套','餐板','其他'],
      partItems:['腰部','足踝','手腕','膝部','軀幹'],
      zoneItems:['白班','小夜','大夜'],
      hourItems:['≦1小時','>1小時 ≦4小時','>4小時 ≦8小時','>8小時 ≦16小時','>16小時 ≦24小時'],
      freereasonItems:['認知改善','情緒穩定','病情惡化','死亡','其他']

    };
  },

  methods: {
    addProcess() {
      this.processType = "add";
      this.title = "新增資料";
      this.titleStyle = "font-weight-black error lighten-2";
      this.list = {
        Date: dayjs().format("YYYY-MM-DD")
      };
      this.$nextTick(() => {
        this.$refs.form.resetValidation();
      });

    },
    editProcess(item) {
      this.dialog = true;
      this.processType = "edit";
      this.title = "修改資料";
      this.titleStyle = "font-weight-black success lighten-2";
      this.list = JSON.parse(JSON.stringify(item)); //複制一份實際修改資料內容
    },
    addOK() {
      //確認新增
      if (this.$refs.form.validate()) {
        this.list.userData = {
          snkey : this.$store.state.uData.snkey,
          name : this.$store.state.uData.name,
          bed_name : this.$store.state.uData.bed_name,
          sex : this.$store.state.uData.sex,
          age : this.thisYear - this.$store.state.uData.birthday.split('-')[0],
          hadadisease : this.$store.state.uData.hadadisease
        } 
        this.list.create_man = this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")";
        this.list.create_man_snkey  = this.$store.state.pData.snkey;
        var postData = {
          datalist: JSON.stringify(this.list)
        };
        this.loading = true;
        this.$api.options("general/add/" + this.$store.state.databaseName + "/" + this.useDataBase,postData)
          .then((rs) => {
            if (rs.state == 1) {
              var pop = { msg: "已新增", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.$emit("getAllData");
              this.dialog = false;
              this.loading = false;
              
            }
          });
      } else {
        var pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
    editOK() {
      //確認修改
      if (this.$refs.form.validate()) {
        if (!this.list.edit_man){this.list.edit_man = ''}
        this.list.edit_man = this.$store.state.pData.username + "(" + dayjs().format("YYYY-MM-DD HH:mm:ss") + ")" + this.list.edit_man;
        let postData = {
          snkey: this.list.snkey,
          datalist: JSON.stringify(this.list),
          edit_man: 'pass'
        };

        this.loading = true;
        this.$api.options("general/edit/" + this.$store.state.databaseName + "/" + this.useDataBase,postData)
          .then((rs) => {
            if (rs.state == 1) {
              let pop = { msg: "已修改", type: true, theme: "success" };
              this.$store.commit("snackbar", pop);
              this.$emit("getAllData");
              this.loading = false;
              this.dialog = false;
            }
          });
      } else {
        let pop = {
          msg: "資料輸入不完全!!請重新確認!!",
          type: true,
          theme: "warning",
        };
        this.$store.commit("snackbar", pop);
      }
    },
  },
};
</script>

<style scoped>
  h2{
    border-radius: 5px;
  }
</style>